.card {
  color: rgb(255, 255, 255);
  height: 78vh;
  background-color: rgb(16, 14, 25);
  border-radius: 8px;
  padding: 20px;
}

.name {
  width: 100%;
  height: 40px;
  text-align: center;
  font-size: 2rem;
  font-weight: 600;
}

.info {
  display: flex;
  justify-content: space-between;
  font-size: 17px;
}

.info2 {
  min-width: 300px;
  max-width: 100%;
  min-height: 50px;
  width: 100%;
  color: white;
  background-color: rgb(31, 27, 46);
  font-size: 17px;
  resize: none;
  border: none;
}

.info2::-webkit-scrollbar {
  height: 0;
}

.textBlock {
  background-color: rgb(31, 27, 46);
  padding: 20px;
  border-radius: 6px;
  margin-left: 10px;
  margin-bottom: 10px;
  width: 50vw;
}

.picture {
  width: 300px;
  height: 600px;
  position: left;
  object-fit: cover;
  object-position: 50%;
  border-radius: 6px;
  border: 6px solid rgb(75, 69, 122);
}

.interactive {
  display: flex;
  justify-content: space-between;
  height: 20px;
  padding-top: 10px;
}

.text {
  height: 95%;
  width: 100%;
}

.text > hr {
  margin-bottom: 10px;
}

.text > p {
  margin-bottom: 10px;
}

.btnGroup {
  display: flex;
  justify-content: flex-end;
  height: 50px;
  bottom: 0;
}

.btn2 {
  display: none;
}

.descriptionElement {
  display: flex;
  align-items: baseline;
}

.descriptionElement > p {
  margin-right: 5px;
}

.btnAdd {
  width: 200px;
}

.btnInteractive {
  background-color: gray;
}