.header {
  margin-bottom: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 300px;
}

.library {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}

.block {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-size: 70px;
  margin-bottom: 25px;
  width: 280px;
  height: 396px;
  border: 2px solid rgba(240, 255, 255, 0.4);
  color: azure;
  box-shadow: 5px 5px 10px 5px rgba(0, 0, 0, 0.4);
  transition: transform 0.2s;
  margin-right: 0.5vw;
  margin-left: 0.5vw;
}

/* .block:hover {
  transform: scale(1.05);
} */

.btnHidden {
  width: 100%;
  height: 100%;
  font-size: 70px;
  background-color: rgb(52, 49, 73);
}