.loginForm {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  padding: 15px;
  width: 30%;
  border-radius: 12px;
  color: azure;
  background-color: rgba(49, 59, 74, 0.7);
}

.input {
  width: 90%;
  margin-bottom: 45px;
}

.btnGroup {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  margin-bottom: 30px;
  font-size: 26px;
  font-weight: bold;
  text-align: center;
  text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
}

.errorText {
  color: rgb(255, 108, 108);
  margin-bottom: 10px;
  text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
  font-size: 1.1rem;
}

.isError {
  margin-bottom: 12px;
}
