.input {
  width: 400px;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  font-size: 1.3rem;
  color: azure;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}

